import React from 'react';
import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom';

const { Header } = Layout;

//        
// const items1 = ['HOME', ''].map((key, test) => ({
//   key,
//   label: <a href={
//     key == 'HOME' ? '/' : key
//   }>{key}</a>,
// }));

//      
const items = [{
  key: 'Home',
  label: (<a>Home</a>),
  link: '/movie'
}];

const NavBar = () => {
  return (
    <Layout>
      <Header
        style={{ display: 'flex', alignItems: 'center', }}>
        <div className="demo-logo" />
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={['Home']}
          items={items}
          style={{ flex: 1, minWidth: 0 }}
        >
          <Menu.Item key={items[0].key}>
            <Link to={items[0].link}>{items[0].label}</Link>
          </Menu.Item>
        </Menu>
      </Header>
    </Layout>
  );
};

export default NavBar;